g-top:10px;padding-bottom:10px;padding-right:10px}.App .code-container .code-line{position:relative;word-break:break-all}.App .code-container .code-line:after{content:attr(data-line-no);position:absolute;left:-35px;color:#3b3553;font-size:16px;line-height:1.6;z-index:3;top:0}.App .code-container .code-line *{display:inline;color:#f9f9f9}.App .code-container .code-line .pr{padding-right:5px}.App .code-container .code-line .pl{padding-left:5px}.App .code-container .code-line .methods{color:#5918df}.App .code-container .code-line .propname{color:#eeff31}.App .code-container .code-line .dots{color:#3b3553}.App .code-container .code-line .comment{color:#78707e}.App .code-container .code-line .propname2{color:#24e394}.App .code-container .code-line .scope{color:#e3244c}.App .code-container .code-line .string{color:#24e33a}.App .code-container .code-line .number{color:#e39d24}.App .code-container .code-line .cursor:before{opacity:0;content:"|";-webkit-animation:blink .8s infinite;animation:blink .8s infinite}@-webkit-keyframes blink{to{opacity:0}}@keyframes blink{0%{opacity:0}50%{opacity:1}to{opacity:0}}.lighbox-caption{font-family:'Itim', cursive;color:#fff}.lighbox-caption a{color:#ffe123}

